import React, {useState, useEffect} from "react";
import Breadcrumb from "../Breadcrumb";
import { Container, Row, Col, Table } from "react-bootstrap";
//import rating from "../../../assets/images/rating.svg";
import placeholderImg from "../../../assets/images/placeholderImg.svg";

import prevArrow from "../../../assets/images/prev.svg";
import nextArrow from "../../../assets/images/next.svg";
import { apiGet, apiPost, axiosWithAuth } from "../../../utilities/axiosInstance";
import API_ENDPOINTS from "../../../utilities/apiEndPoints";
import { useAuth } from "../../../context/AuthContext";
//import Loader from "../../../Components/Loader";
import { useNavigate } from 'react-router-dom';

import { Link } from "react-router-dom";



const getStatusClass = (status) => {
  if (status.includes("canceled")) return "text-red";
  if (status.includes("confirmed")) return "text-green";
  return "text-secondary";
};

export default function Payment() {

  const [isLoading, setIsLoading]= useState(false);
  const [rating, setRating] = useState({})
  const [payout, setPayout] = useState({})
// booking history
const { token } = useAuth();
const [historyBookings, setHistoryBookings]= useState([])
const [pendingBookings, setPendingBookings] = useState([])
const [tablePage, setTablePage] = useState(1)

const clickTablepage = (nowpage) => { setTablePage(nowpage) };
const navigate = useNavigate();
useEffect(() => {
// history booking
 const fetchTrainerBookingData = async () => {
   setIsLoading(true)
   try {
      const authInstance = axiosWithAuth(token); // Pass the token to create the instance 
   const responseNew = await authInstance.get(`${API_ENDPOINTS.BOOKING_LIST}?status=history`);
      const response = await responseNew.data
 // console.log(response)
     if (response.success && response.data) {
       setIsLoading(false)
      // console.log(response)   
       setHistoryBookings(response.data)  
     } else {
       console.error("Unexpected response format:", response);
     }
   } catch (error) {
     console.error("Error fetching pricing data:", error);
   }
   finally{
    setIsLoading(false)
  }
 };

 fetchTrainerBookingData();
 // history booking
 //pending booking
 const fetchTrainerPendingData = async () => {
  setIsLoading(true)
  try {
     const authInstance = axiosWithAuth(token); // Pass the token to create the instance 
  const responseNew = await authInstance.get(`${API_ENDPOINTS.BOOKING_LIST}?status=pending`);
     const response = await responseNew.data
 //console.log(response)
    if (response.success && response.data) {
      setIsLoading(false)
      //console.log(response)   
      setPendingBookings(response.data)  
    } else {
      console.error("Unexpected response format:", response);
    }
  } catch (error) {
    console.error("Error fetching pricing data:", error);
  }
  finally{
   setIsLoading(false)
 }
};

fetchTrainerPendingData();
 //pending booking
 //review  and price
 const fetchTrainerDashData = async () => {
  setIsLoading(true)
  try {
 //   const authToken = `Bearer ${token}`;
   // const response = await apiGet(API_ENDPOINTS.TRAINER_DASHBOARD, {headers: { 
//  Authorization: authToken , 
//}});
  const authInstance = axiosWithAuth(token); // Pass the token to create the instance
  const responseNew = await authInstance.get(API_ENDPOINTS.TRAINER_DASHBOARD);
   const response = await responseNew.data
 //console.log(response)
    if (response.success && response.data) {
      setIsLoading(false)
     // console.log(response)
      setRating(response.data.user_rating)
      setPayout(response.data.fortnight_payouts)
   //   setPricingData(response.data.hours_list);
    } else {
      console.error("Unexpected response format:", response);
    }
  } catch (error) {
    console.error("Error fetching pricing data:", error);
  }
  finally{
    setIsLoading(false)
  }
};

fetchTrainerDashData();
//  reviews and price
},[])
//booking history
//  console.log(pendingBookings);
  return (
    <div>
      <div className="mainContent">
        <Breadcrumb />
        <div className="mainContent__assets">
          <Container>
            <Row>
              <Col lg={12}>
                <div className="accordians mb-5">
                  <div className="secIntro ">
                    <div className="secIntro__left">
                      <h2>Payments</h2>
                    </div>
                  </div>
                  <div className="accordians__card">
                    <div className="accordians__top">
                      <h4>Unauthorised Payments</h4>
                      <Link
                        to="/dashboard/booking-history"
                        className="btn btn-teal invisible"
                      >
                        View All
                      </Link>
                    </div>
                    <div className="accordians__description">
                      <Table>
                        <thead>
                          <tr>
                            <th>Booking</th>
                            <th>Learner</th>
                            <th>Date</th>
                            <th>Time</th>
                            <th>Location</th>
                            <th>Status</th>
                          </tr>
                        </thead>
                        <tbody>
                        {isLoading ? <tr><td>Loading...</td></tr> :
                          // pendingBookings.map((booking, index) => (
                            pendingBookings.length > 0 ? pendingBookings.map((booking, index) =>(
                            <tr key={index}>
                              <td>{booking.bookingNumber}</td>
                              <td>
                                <img
                                  src={booking?.learners?.profile_picture || placeholderImg}
                                  alt="profile"
                                  className="rounded-circle mr-2"
                                />{" "}
                                {/* {booking?.instructor?.first_name} */}
                                <span className="str-reduce">{booking?.learners?.myself === 1 ? `${booking?.learners?.first_name} ${booking?.learners?.last_name}` : `${booking?.learners?.learner_first_name} ${booking?.learners?.learner_last_name}`}
                                </span>
                              </td>
                              <td>{booking?.lesson_details?.lesson_date}</td>
                              <td>{booking?.lesson_details?.lesson_time}</td>
                              <td>{booking?.learners?.address}</td>
                              <td>
                                <Link className="btn btn-teal" to="/instructor/payments/payment-authorisation" state={{id:booking.id}}>
                                  Authorize Payment
                                </Link>
                              </td>
                            </tr>
                          )) : (<tr><td>No Data found</td></tr>)}
                        </tbody>
                      </Table>
                    </div>
                  </div>
                </div>
                {/* Dashboard */}
                <div className="dashboard">
                  <div className="secIntro ">
                    {/* <div className="secIntro__left">
                      <h2>Payments</h2>
                    </div> */}
                  </div>
                  <div className="dashboard__wrp">
                    <div className="dashboard__left">
                      <div className="dashboard__topTitle">
                        <p>All Time Performance</p>
                      </div>
                      <div className="dashboard__bottom">
                        <div className="dashboard__item">
                          <span>Learner Rating</span>
                          <h4>
                            <img src={rating} alt="" /> 0{" "}
                            <span>({rating?.total_reviews} Reviews)</span>
                          </h4>
                        </div>
                      </div>
                    </div>
                    <div className="dashboard__right">
                      <div className="dashboard__topTitle" style={{textAlign:'center'}}>
                        <p>Current Payout</p>
                      </div>
                      <div className="dashboard__bottom">
                        <div className="dashboard__item">
                          <span>Earnings </span>
                          <h4>${payout?.earnings?.amount}</h4>
                          <p>Av. Fortnightly ${payout?.earnings?.avg_amount}</p>
                        </div>
                        <div className="dashboard__item">
                          <span>Booking hours completed </span>
                          <h4>{payout?.completed_booking?.amount}</h4>
                          <p>Av. Fortnightly {payout?.completed_booking?.avg_amount}</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Accordians */}
                <div className="accordians mt-5">
                  <div className="accordians__card">
                    <div className="accordians__top">
                      <h4>Booking History</h4>
                      <Link
                        to="/instructor/dashboard/booking-history"
                        className="btn btn-teal"
                      >
                        View All
                      </Link>
                    </div>
                    <div className="accordians__description">
                      <Table>
                        <thead>
                          <tr>
                            <th>Booking</th>
                            <th>Learner</th>
                            <th>Date</th>
                            <th>Time</th>
                            <th>Location</th>
                            <th>Status</th>
                          </tr>
                        </thead>
                        <tbody>
                        {isLoading ? <tr><td>Loading...</td></tr> :
                      
                            historyBookings.length> 0 ? historyBookings.slice(tablePage * 5 - 5, tablePage * 5).map((booking, index) =>(
                         // {historyBookings.map((booking, index) => (
                            <tr key={index}>
                              <td>{booking.bookingNumber}</td>
                              <td>
                                <img
                                  src={booking?.instructor?.profile_image || placeholderImg}
                                  alt="profile"
                                  className="rounded-circle mr-2"
                                />{" "}
                                {booking?.instructor?.first_name}
                              </td>
                              <td>{booking?.lesson_details?.lesson_date}</td>
                              <td>{booking?.lesson_details?.lesson_time}</td>
                              <td>{booking?.learners?.address}</td>
                              <td
                                className={`font-weight-bold ${getStatusClass(
                                  booking?.booking_status
                                )}`}
                              >
                                <div className="d-flex align-items-center">
                                  <div className="statusIco"></div>
                                  {booking.booking_status}
                                </div>
                              </td>
                            </tr>
                          )): (<tr><td>No Data found</td></tr>)}
                        </tbody>
                      </Table>
                     {/* Pagination */}
                     <div className='pagination-container'>
                            <button className="btn " onClick={() => { clickTablepage(tablePage - 1) }} 
                              style={{'pointer-events':tablePage === 1 ? 'none' : ''}}  >
                              <img src={prevArrow} alt="" />
                            </button>
                            {historyBookings.length > 5 ? <span className={tablePage === 1 ? "active_tab" : "pagetab"} onClick={() => clickTablepage(1)}> 1 </span> :'' }
                     
                          {/* Left Ellipsis */}
                            {tablePage > 3 && <span className="pagetab"> ... </span>}
                         
                          {/* Visible Pages */}
                            {[...Array(3)].map((_, i) => {
                                const page = tablePage - 1 + i; // Adjust the visible pages around the current page
                                return ( page > 1 &&
                                    page < Math.ceil(historyBookings.length / 5) && (
                                        <span key={page} className={tablePage === page ? "active_tab" : ""} onClick={() => clickTablepage(page)}>
                                            {page}
                                        </span>
                                    )
                                );
                            })}

                            {/* Right Ellipsis */}
                            {tablePage < Math.ceil(historyBookings.length / 5) - 2 && ( <span className="pagetab"> ... </span> )}

                            {/* Last Page */}
                            <span className={ tablePage === Math.ceil(historyBookings.length / 5) ? "active_tab" : "pagetab" }
                                onClick={() => clickTablepage(Math.ceil(historyBookings.length / 5))}
                            >
                                {Math.ceil(historyBookings.length / 5)}
                            </span>

                          <button className="btn " 
                             style={{'pointer-events': tablePage === Math.ceil(historyBookings.length / 5) ? 'none' : ''}} 
                             onClick={() => { clickTablepage(tablePage + 1); }}>
                             <img src={nextArrow} alt="" />
                          </button>
                          </div>


                    </div>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </div>
  );
}
